export default {
  // 此处编写Vue组件实例的配置项，通过一定语法，直接混入组件内部
  // data methods conputed 生命周期函数 ...
  // 和组件内，提供了同名的data或methods,则组件内优先级更高
  data () {
    return {
      title: '标题'
    }
  },
  methods: {
    sayHi () {
      console.log('你好')
    },
    // 根据登录状态，判断是否摇显示登录确认框
    loginConfirm () {
      // 判断token是否存在
      if (!this.$store.getters.token) {
        // 弹确认框
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能操作哦',
          confirmButtonText: '去登陆',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // 如果希望，跳转到登录 => 登录后调回来，需跳去携带参数
            // this.$route.fullPath(包含查询参数)
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => {})
        return true
      }
      return false
    }
  }
}
